"use client";

import { useRouter } from "next/navigation";

export function VideosEmptyState() {
  const router = useRouter();

  return (
    <div className="group relative overflow-hidden rounded-3xl border-2 border-dashed border-green-200 bg-gradient-to-br from-green-50/50 to-emerald-50/50 p-12 transition-all hover:border-green-300 hover:shadow-lg">
      <div className="relative z-10 flex flex-col items-center text-center">
        <div className="mb-6 flex h-24 w-24 items-center justify-center rounded-full bg-green-100 transition-transform group-hover:scale-110">
          <svg
            xmlns="http://www.w3.org/2000/svg"
            className="h-12 w-12 text-green-600"
            fill="none"
            viewBox="0 0 24 24"
            stroke="currentColor"
            strokeWidth={1.5}
          >
            <path
              strokeLinecap="round"
              strokeLinejoin="round"
              d="M15.75 10.5l4.72-4.72a.75.75 0 011.28.53v11.38a.75.75 0 01-1.28.53l-4.72-4.72M4.5 18.75h9a2.25 2.25 0 002.25-2.25v-9a2.25 2.25 0 00-2.25-2.25h-9A2.25 2.25 0 002.25 7.5v9a2.25 2.25 0 002.25 2.25z"
            />
          </svg>
        </div>
        <h3 className="mb-2 text-xl font-bold text-gray-900">
          Создайте свое первое видео
        </h3>
        <p className="mb-6 max-w-sm text-sm text-gray-600">
          Используйте аватары и голоса для создания профессиональных видео с помощью нашего редактора
        </p>
        <button
          type="button"
          onClick={() => router.push("/material/editor")}
          className="rounded-xl bg-green-600 px-6 py-3 text-sm font-semibold text-white shadow-md transition-all hover:bg-green-700 hover:shadow-lg active:scale-95"
        >
          Создать видео
        </button>
      </div>
      <div className="absolute inset-0 opacity-5">
        <div className="absolute left-10 top-10 h-32 w-32 rounded-full bg-green-400 blur-3xl" />
        <div className="absolute bottom-10 right-10 h-40 w-40 rounded-full bg-emerald-400 blur-3xl" />
      </div>
    </div>
  );
}

